"use client";

import React, { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { ScrollArea } from "@/components/ui/scroll-area";
import { useGame } from "@/lib/gameContext";

interface Enemy {
  id: string;
  name: string;
  level: number;
  health: number;
  maxHealth: number;
  attack: number;
  defense: number;
  type: 'player' | 'monster' | 'boss';
  faction?: string;
}

const enemies: Enemy[] = [
  {
    id: 'shadow-warrior',
    name: 'Guerrier de l\'Ombre',
    level: 2,
    health: 80,
    maxHealth: 80,
    attack: 12,
    defense: 8,
    type: 'player',
    faction: 'Légion de l\'Ombre'
  },
  {
    id: 'corrupted-beast',
    name: 'Bête Corrompue',
    level: 3,
    health: 120,
    maxHealth: 120,
    attack: 15,
    defense: 5,
    type: 'monster'
  },
  {
    id: 'eclipse-guardian',
    name: 'Gardien de l\'Éclipse',
    level: 5,
    health: 200,
    maxHealth: 200,
    attack: 20,
    defense: 15,
    type: 'boss'
  }
];

type CombatAction = 'attack' | 'defend' | 'special' | 'flee';

export default function CombatArena() {
  const { state, dispatch } = useGame();
  const [currentEnemy, setCurrentEnemy] = useState<Enemy | null>(null);
  const [playerTurn, setPlayerTurn] = useState(true);
  const [combatEnded, setCombatEnded] = useState(false);
  const [isAnimating, setIsAnimating] = useState(false);
  const [specialCooldown, setSpecialCooldown] = useState(0);

  useEffect(() => {
    if (specialCooldown > 0) {
      const timer = setTimeout(() => setSpecialCooldown(specialCooldown - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [specialCooldown]);

  const startCombat = (enemy: Enemy) => {
    setCurrentEnemy({ ...enemy });
    setCombatEnded(false);
    setPlayerTurn(true);
    setSpecialCooldown(0);
    dispatch({ type: 'SET_COMBAT_STATE', payload: true });
    dispatch({ type: 'CLEAR_COMBAT_LOG' });
    dispatch({ type: 'ADD_COMBAT_LOG', payload: `Combat contre ${enemy.name} commence !` });
  };

  const endCombat = (victory: boolean) => {
    setCombatEnded(true);
    dispatch({ type: 'SET_COMBAT_STATE', payload: false });
    
    if (victory && currentEnemy && state.character) {
      const expGain = currentEnemy.level * 15;
      const goldGain = currentEnemy.level * 10;
      
      dispatch({ type: 'UPDATE_CHARACTER', payload: { 
        experience: state.character.experience + expGain 
      }});
      dispatch({ type: 'ADD_TO_INVENTORY', payload: `${goldGain} pièces d'or` });
      dispatch({ type: 'ADD_COMBAT_LOG', payload: `Victoire ! +${expGain} EXP, +${goldGain} or` });
    }
  };

  const calculateDamage = (attacker: { attack: number }, defender: { defense: number }) => {
    const baseDamage = attacker.attack;
    const defense = defender.defense;
    const damage = Math.max(1, baseDamage - Math.floor(defense / 2) + Math.floor(Math.random() * 10) - 5);
    return damage;
  };

  const performAction = async (action: CombatAction) => {
    if (!currentEnemy || !state.character || !playerTurn || isAnimating || combatEnded) return;

    setIsAnimating(true);
    setPlayerTurn(false);

    let damage = 0;
    let actionText = '';

    switch (action) {
      case 'attack':
        damage = calculateDamage(state.character, currentEnemy);
        actionText = `Vous attaquez ${currentEnemy.name} pour ${damage} dégâts !`;
        setCurrentEnemy(prev => prev ? { ...prev, health: Math.max(0, prev.health - damage) } : null);
        break;

      case 'defend':
        actionText = 'Vous vous mettez en défense, réduisant les dégâts subis.';
        // Heal a small amount
        const healAmount = Math.floor(state.character.maxHealth * 0.1);
        dispatch({ type: 'UPDATE_CHARACTER', payload: { 
          health: Math.min(state.character.maxHealth, state.character.health + healAmount)
        }});
        break;

      case 'special':
        if (specialCooldown > 0) {
          actionText = `Compétence spéciale en recharge (${specialCooldown}s)`;
        } else {
          damage = calculateDamage({ attack: state.character.attack * 1.5 }, currentEnemy);
          actionText = `Attaque spéciale ! Vous infligez ${damage} dégâts critiques !`;
          setCurrentEnemy(prev => prev ? { ...prev, health: Math.max(0, prev.health - damage) } : null);
          setSpecialCooldown(5);
        }
        break;

      case 'flee':
        actionText = 'Vous fuyez le combat !';
        endCombat(false);
        setIsAnimating(false);
        return;
    }

    dispatch({ type: 'ADD_COMBAT_LOG', payload: actionText });

    // Check if enemy is defeated
    if (currentEnemy && currentEnemy.health - damage <= 0) {
      setTimeout(() => {
        endCombat(true);
        setIsAnimating(false);
      }, 1000);
      return;
    }

    // Enemy turn
    setTimeout(() => {
      if (currentEnemy && currentEnemy.health > 0) {
        const enemyDamage = calculateDamage(currentEnemy, state.character!);
        const enemyActionText = `${currentEnemy.name} vous attaque pour ${enemyDamage} dégâts !`;
        
        dispatch({ type: 'ADD_COMBAT_LOG', payload: enemyActionText });
        dispatch({ type: 'UPDATE_CHARACTER', payload: { 
          health: Math.max(0, state.character!.health - enemyDamage)
        }});

        // Check if player is defeated
        if (state.character!.health - enemyDamage <= 0) {
          setTimeout(() => {
            endCombat(false);
            dispatch({ type: 'ADD_COMBAT_LOG', payload: 'Défaite... Vous êtes tombé au combat.' });
            // Reset player health to 1 to avoid game over
            dispatch({ type: 'UPDATE_CHARACTER', payload: { health: 1 } });
          }, 1000);
        } else {
          setPlayerTurn(true);
        }
      }
      setIsAnimating(false);
    }, 1500);
  };

  if (!state.character) {
    return (
      <div className="text-center py-12">
        <p className="text-slate-400">Vous devez créer un personnage avant d'entrer dans l'arène.</p>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-amber-400 mb-2">Arène de Combat</h2>
        <p className="text-slate-300">
          Affrontez vos ennemis dans des duels épiques et prouvez votre valeur
        </p>
      </div>

      <div className="grid lg:grid-cols-3 gap-6">
        {/* Enemy Selection */}
        {!currentEnemy && (
          <div className="lg:col-span-3">
            <Card className="bg-slate-800/50 border-slate-600 mb-6">
              <CardHeader>
                <CardTitle className="text-white">Choisissez votre Adversaire</CardTitle>
                <CardDescription>Sélectionnez un ennemi à affronter</CardDescription>
              </CardHeader>
            </Card>
            
            <div className="grid md:grid-cols-3 gap-4">
              {enemies.map((enemy) => {
                const canFight = state.character.level >= enemy.level - 1;
                
                return (
                  <Card 
                    key={enemy.id}
                    className={`cursor-pointer transition-all hover:scale-105 ${
                      canFight ? 'bg-slate-800/50 border-slate-600' : 'bg-slate-800/20 border-slate-700 opacity-50'
                    }`}
                    onClick={() => canFight && startCombat(enemy)}
                  >
                    <CardHeader>
                      <CardTitle className={`text-lg ${
                        enemy.type === 'boss' ? 'text-red-400' : 
                        enemy.type === 'player' ? 'text-blue-400' : 'text-orange-400'
                      }`}>
                        {enemy.name}
                      </CardTitle>
                      <CardDescription>
                        Niveau {enemy.level} • {enemy.faction || 'Créature sauvage'}
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-2 text-sm">
                        <div className="flex justify-between">
                          <span className="text-slate-400">PV:</span>
                          <span className="text-green-400">{enemy.health}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-slate-400">Attaque:</span>
                          <span className="text-red-400">{enemy.attack}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-slate-400">Défense:</span>
                          <span className="text-blue-400">{enemy.defense}</span>
                        </div>
                      </div>
                      {!canFight && (
                        <p className="text-red-400 text-xs mt-2">
                          Niveau insuffisant
                        </p>
                      )}
                    </CardContent>
                  </Card>
                );
              })}
            </div>
          </div>
        )}

        {/* Combat Interface */}
        {currentEnemy && (
          <>
            {/* Player Stats */}
            <Card className="bg-slate-800/50 border-slate-600">
              <CardHeader>
                <CardTitle className="text-blue-400">{state.character.name}</CardTitle>
                <CardDescription>Niveau {state.character.level}</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span>Points de Vie</span>
                      <span className="text-green-400">
                        {state.character.health}/{state.character.maxHealth}
                      </span>
                    </div>
                    <Progress 
                      value={(state.character.health / state.character.maxHealth) * 100} 
                      className="h-2"
                    />
                  </div>
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span>Énergie</span>
                      <span className="text-blue-400">
                        {state.character.energy}/{state.character.maxEnergy}
                      </span>
                    </div>
                    <Progress 
                      value={(state.character.energy / state.character.maxEnergy) * 100} 
                      className="h-2"
                    />
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Combat Actions */}
            <Card className="bg-slate-800/50 border-slate-600">
              <CardHeader>
                <CardTitle className="text-amber-400">Actions de Combat</CardTitle>
                <CardDescription>
                  {playerTurn ? 'À votre tour !' : 'Tour de l\'ennemi...'}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 gap-2">
                  <Button
                    onClick={() => performAction('attack')}
                    disabled={!playerTurn || isAnimating}
                    className="bg-red-600 hover:bg-red-700"
                  >
                    Attaquer
                  </Button>
                  <Button
                    onClick={() => performAction('defend')}
                    disabled={!playerTurn || isAnimating}
                    className="bg-blue-600 hover:bg-blue-700"
                  >
                    Défendre
                  </Button>
                  <Button
                    onClick={() => performAction('special')}
                    disabled={!playerTurn || isAnimating || specialCooldown > 0}
                    className="bg-purple-600 hover:bg-purple-700"
                  >
                    Spécial {specialCooldown > 0 && `(${specialCooldown}s)`}
                  </Button>
                  <Button
                    onClick={() => performAction('flee')}
                    disabled={!playerTurn || isAnimating}
                    variant="outline"
                    className="border-slate-600 text-slate-300 hover:bg-slate-700"
                  >
                    Fuir
                  </Button>
                </div>
                
                {combatEnded && (
                  <Button
                    onClick={() => setCurrentEnemy(null)}
                    className="w-full mt-4 bg-amber-600 hover:bg-amber-700"
                  >
                    Nouveau Combat
                  </Button>
                )}
              </CardContent>
            </Card>

            {/* Enemy Stats */}
            <Card className="bg-slate-800/50 border-slate-600">
              <CardHeader>
                <CardTitle className="text-red-400">{currentEnemy.name}</CardTitle>
                <CardDescription>Niveau {currentEnemy.level}</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span>Points de Vie</span>
                      <span className="text-green-400">
                        {currentEnemy.health}/{currentEnemy.maxHealth}
                      </span>
                    </div>
                    <Progress 
                      value={(currentEnemy.health / currentEnemy.maxHealth) * 100} 
                      className="h-2"
                    />
                  </div>
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <div>
                      <span className="text-slate-400">Attaque:</span>
                      <p className="text-red-400 font-semibold">{currentEnemy.attack}</p>
                    </div>
                    <div>
                      <span className="text-slate-400">Défense:</span>
                      <p className="text-blue-400 font-semibold">{currentEnemy.defense}</p>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Combat Log */}
            <Card className="lg:col-span-3 bg-slate-800/50 border-slate-600">
              <CardHeader>
                <CardTitle className="text-white">Journal de Combat</CardTitle>
              </CardHeader>
              <CardContent>
                <ScrollArea className="h-32">
                  <div className="space-y-1">
                    {state.combatLog.map((log, index) => (
                      <p key={index} className="text-sm text-slate-300">
                        {log}
                      </p>
                    ))}
                  </div>
                </ScrollArea>
              </CardContent>
            </Card>
          </>
        )}
      </div>
    </div>
  );
}
