"use client";

import React, { createContext, useContext, useReducer, ReactNode } from 'react';

// Types
export interface Character {
  name: string;
  faction: string;
  class: string;
  level: number;
  health: number;
  maxHealth: number;
  energy: number;
  maxEnergy: number;
  experience: number;
  attack: number;
  defense: number;
}

export interface Quest {
  id: string;
  title: string;
  description: string;
  status: 'active' | 'completed' | 'failed';
  progress: number;
  maxProgress: number;
}

export interface GameState {
  character: Character | null;
  currentLocation: string;
  quests: Quest[];
  inventory: string[];
  combatLog: string[];
  isInCombat: boolean;
  npcDialogue: Array<{ speaker: string; message: string; timestamp: number }>;
}

// Actions
type GameAction =
  | { type: 'CREATE_CHARACTER'; payload: Omit<Character, 'level' | 'health' | 'maxHealth' | 'energy' | 'maxEnergy' | 'experience'> }
  | { type: 'UPDATE_CHARACTER'; payload: Partial<Character> }
  | { type: 'SET_LOCATION'; payload: string }
  | { type: 'ADD_QUEST'; payload: Quest }
  | { type: 'UPDATE_QUEST'; payload: { id: string; updates: Partial<Quest> } }
  | { type: 'ADD_TO_INVENTORY'; payload: string }
  | { type: 'ADD_COMBAT_LOG'; payload: string }
  | { type: 'CLEAR_COMBAT_LOG' }
  | { type: 'SET_COMBAT_STATE'; payload: boolean }
  | { type: 'ADD_NPC_MESSAGE'; payload: { speaker: string; message: string } }
  | { type: 'CLEAR_NPC_DIALOGUE' };

// Initial state
const initialState: GameState = {
  character: null,
  currentLocation: 'Terres Désolées',
  quests: [],
  inventory: [],
  combatLog: [],
  isInCombat: false,
  npcDialogue: [],
};

// Reducer
function gameReducer(state: GameState, action: GameAction): GameState {
  switch (action.type) {
    case 'CREATE_CHARACTER':
      return {
        ...state,
        character: {
          ...action.payload,
          level: 1,
          health: 100,
          maxHealth: 100,
          energy: 50,
          maxEnergy: 50,
          experience: 0,
          attack: 10,
          defense: 8,
        },
      };
    
    case 'UPDATE_CHARACTER':
      return {
        ...state,
        character: state.character ? { ...state.character, ...action.payload } : null,
      };
    
    case 'SET_LOCATION':
      return {
        ...state,
        currentLocation: action.payload,
      };
    
    case 'ADD_QUEST':
      return {
        ...state,
        quests: [...state.quests, action.payload],
      };
    
    case 'UPDATE_QUEST':
      return {
        ...state,
        quests: state.quests.map(quest =>
          quest.id === action.payload.id
            ? { ...quest, ...action.payload.updates }
            : quest
        ),
      };
    
    case 'ADD_TO_INVENTORY':
      return {
        ...state,
        inventory: [...state.inventory, action.payload],
      };
    
    case 'ADD_COMBAT_LOG':
      return {
        ...state,
        combatLog: [...state.combatLog, action.payload],
      };
    
    case 'CLEAR_COMBAT_LOG':
      return {
        ...state,
        combatLog: [],
      };
    
    case 'SET_COMBAT_STATE':
      return {
        ...state,
        isInCombat: action.payload,
      };
    
    case 'ADD_NPC_MESSAGE':
      return {
        ...state,
        npcDialogue: [
          ...state.npcDialogue,
          {
            ...action.payload,
            timestamp: Date.now(),
          },
        ],
      };
    
    case 'CLEAR_NPC_DIALOGUE':
      return {
        ...state,
        npcDialogue: [],
      };
    
    default:
      return state;
  }
}

// Context
const GameContext = createContext<{
  state: GameState;
  dispatch: React.Dispatch<GameAction>;
} | null>(null);

// Provider
export function GameProvider({ children }: { children: ReactNode }) {
  const [state, dispatch] = useReducer(gameReducer, initialState);

  return (
    <GameContext.Provider value={{ state, dispatch }}>
      {children}
    </GameContext.Provider>
  );
}

// Hook
export function useGame() {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error('useGame must be used within a GameProvider');
  }
  return context;
}
