"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { useGame } from "@/lib/gameContext";

interface Location {
  id: string;
  name: string;
  description: string;
  type: 'city' | 'dungeon' | 'wilderness' | 'ruins';
  level: number;
  rewards: string[];
  explored: boolean;
  danger: 'low' | 'medium' | 'high' | 'extreme';
}

const locations: Location[] = [
  {
    id: 'terres-desolees',
    name: 'Terres Désolées',
    description: 'Votre point de départ. Les vestiges de l\'ancienne civilisation jonchent ces plaines arides.',
    type: 'wilderness',
    level: 1,
    rewards: ['Fragments de cristal', 'Herbes médicinales'],
    explored: true,
    danger: 'low'
  },
  {
    id: 'ruines-kethara',
    name: 'Ruines de Kethara',
    description: 'Une ancienne cité engloutie par l\'Éclipse. Des murmures étranges résonnent entre ses murs.',
    type: 'ruins',
    level: 3,
    rewards: ['Artefacts anciens', 'Parchemins oubliés', 'Or maudit'],
    explored: false,
    danger: 'medium'
  },
  {
    id: 'foret-ombres',
    name: 'Forêt des Ombres',
    description: 'Les arbres ici ont été corrompus par l\'énergie de l\'Éclipse. Danger et mystères s\'entremêlent.',
    type: 'wilderness',
    level: 2,
    rewards: ['Bois corrompu', 'Essence d\'ombre', 'Champignons lumineux'],
    explored: false,
    danger: 'medium'
  },
  {
    id: 'donjon-eclipse',
    name: 'Donjon de l\'Éclipse',
    description: 'Le cœur des ténèbres. Seuls les plus braves osent s\'aventurer dans ces profondeurs maudites.',
    type: 'dungeon',
    level: 8,
    rewards: ['Fragment du Cœur', 'Armes légendaires', 'Secrets interdits'],
    explored: false,
    danger: 'extreme'
  },
  {
    id: 'bastion-lumiere',
    name: 'Bastion de Lumière',
    description: 'Le dernier refuge des forces de la lumière. Un havre de paix dans ce monde chaotique.',
    type: 'city',
    level: 1,
    rewards: ['Équipements', 'Potions', 'Informations'],
    explored: false,
    danger: 'low'
  },
  {
    id: 'marais-perdus',
    name: 'Marais Perdus',
    description: 'Des créatures étranges hantent ces eaux stagnantes. L\'air lui-même semble vivant.',
    type: 'wilderness',
    level: 4,
    rewards: ['Composants alchimiques', 'Reliques submergées'],
    explored: false,
    danger: 'high'
  }
];

export default function WorldMap() {
  const { state, dispatch } = useGame();
  const [selectedLocation, setSelectedLocation] = useState<Location | null>(null);
  const [isExploring, setIsExploring] = useState(false);
  const [explorationProgress, setExplorationProgress] = useState(0);

  const getDangerColor = (danger: Location['danger']) => {
    switch (danger) {
      case 'low': return 'text-green-400';
      case 'medium': return 'text-yellow-400';
      case 'high': return 'text-orange-400';
      case 'extreme': return 'text-red-400';
    }
  };

  const getDangerText = (danger: Location['danger']) => {
    switch (danger) {
      case 'low': return 'Paisible';
      case 'medium': return 'Modéré';
      case 'high': return 'Dangereux';
      case 'extreme': return 'Mortel';
    }
  };

  const getTypeIcon = (type: Location['type']) => {
    switch (type) {
      case 'city': return '🏰';
      case 'dungeon': return '⚫';
      case 'wilderness': return '🌲';
      case 'ruins': return '🏛️';
    }
  };

  const canExplore = (location: Location) => {
    if (!state.character) return false;
    return state.character.level >= location.level;
  };

  const handleExplore = async (location: Location) => {
    if (!canExplore(location) || isExploring) return;

    setIsExploring(true);
    setExplorationProgress(0);

    // Simulate exploration progress
    const interval = setInterval(() => {
      setExplorationProgress(prev => {
        if (prev >= 100) {
          clearInterval(interval);
          completeExploration(location);
          return 100;
        }
        return prev + 10;
      });
    }, 300);
  };

  const completeExploration = (location: Location) => {
    // Update location
    dispatch({ type: 'SET_LOCATION', payload: location.name });

    // Add rewards to inventory
    location.rewards.forEach(reward => {
      dispatch({ type: 'ADD_TO_INVENTORY', payload: reward });
    });

    // Add experience
    if (state.character) {
      const expGain = location.level * 10;
      dispatch({ 
        type: 'UPDATE_CHARACTER', 
        payload: { experience: state.character.experience + expGain }
      });
    }

    // Update quest progress if applicable
    const welcomeQuest = state.quests.find(q => q.id === 'welcome-quest');
    if (welcomeQuest && welcomeQuest.status === 'active' && welcomeQuest.progress < welcomeQuest.maxProgress) {
      dispatch({
        type: 'UPDATE_QUEST',
        payload: {
          id: 'welcome-quest',
          updates: { progress: welcomeQuest.progress + 1 }
        }
      });
    }

    // Mark location as explored
    location.explored = true;

    setIsExploring(false);
    setExplorationProgress(0);
    setSelectedLocation(null);
  };

  return (
    <div className="max-w-6xl mx-auto">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-amber-400 mb-2">Carte d'Auralis</h2>
        <p className="text-slate-300">
          Explorez les terres mystérieuses marquées par l'Éclipse Noire
        </p>
        <p className="text-sm text-slate-400 mt-2">
          Localisation actuelle: <span className="text-amber-400">{state.currentLocation}</span>
        </p>
      </div>

      {isExploring && (
        <Card className="mb-6 bg-slate-800/50 border-slate-600">
          <CardContent className="pt-6">
            <div className="text-center">
              <p className="text-amber-400 mb-4">Exploration en cours...</p>
              <Progress value={explorationProgress} className="w-full" />
              <p className="text-sm text-slate-400 mt-2">{explorationProgress}% complété</p>
            </div>
          </CardContent>
        </Card>
      )}

      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {locations.map((location) => {
          const isAccessible = canExplore(location);
          const isCurrentLocation = state.currentLocation === location.name;
          
          return (
            <Card 
              key={location.id}
              className={`cursor-pointer transition-all duration-300 hover:scale-105 ${
                isCurrentLocation 
                  ? 'bg-amber-900/30 border-amber-600' 
                  : location.explored 
                    ? 'bg-slate-800/50 border-slate-600' 
                    : 'bg-slate-800/30 border-slate-700'
              } ${!isAccessible ? 'opacity-50' : ''}`}
              onClick={() => !isExploring && setSelectedLocation(location)}
            >
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle className={`text-lg ${isCurrentLocation ? 'text-amber-400' : 'text-white'}`}>
                    {getTypeIcon(location.type)} {location.name}
                  </CardTitle>
                  {location.explored && <span className="text-green-400 text-sm">✓</span>}
                </div>
                <CardDescription className="text-slate-300">
                  {location.description}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span className="text-slate-400">Niveau requis:</span>
                    <span className={isAccessible ? 'text-green-400' : 'text-red-400'}>
                      {location.level}
                    </span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-slate-400">Danger:</span>
                    <span className={getDangerColor(location.danger)}>
                      {getDangerText(location.danger)}
                    </span>
                  </div>
                  <div className="mt-3">
                    <p className="text-xs text-slate-400 mb-1">Récompenses:</p>
                    <div className="flex flex-wrap gap-1">
                      {location.rewards.slice(0, 2).map((reward, index) => (
                        <span key={index} className="text-xs bg-slate-700 px-2 py-1 rounded text-slate-300">
                          {reward}
                        </span>
                      ))}
                      {location.rewards.length > 2 && (
                        <span className="text-xs text-slate-400">+{location.rewards.length - 2}</span>
                      )}
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          );
        })}
      </div>

      {/* Location Detail Modal */}
      {selectedLocation && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <Card className="max-w-md w-full bg-slate-800 border-slate-600">
            <CardHeader>
              <CardTitle className="text-amber-400">
                {getTypeIcon(selectedLocation.type)} {selectedLocation.name}
              </CardTitle>
              <CardDescription className="text-slate-300">
                {selectedLocation.description}
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-slate-400">Niveau requis:</span>
                  <p className={canExplore(selectedLocation) ? 'text-green-400' : 'text-red-400'}>
                    {selectedLocation.level}
                  </p>
                </div>
                <div>
                  <span className="text-slate-400">Danger:</span>
                  <p className={getDangerColor(selectedLocation.danger)}>
                    {getDangerText(selectedLocation.danger)}
                  </p>
                </div>
              </div>
              
              <div>
                <p className="text-slate-400 text-sm mb-2">Récompenses possibles:</p>
                <div className="space-y-1">
                  {selectedLocation.rewards.map((reward, index) => (
                    <div key={index} className="text-sm bg-slate-700 px-3 py-1 rounded text-slate-300">
                      {reward}
                    </div>
                  ))}
                </div>
              </div>

              <div className="flex gap-2">
                <Button
                  onClick={() => handleExplore(selectedLocation)}
                  disabled={!canExplore(selectedLocation) || isExploring}
                  className="flex-1 bg-amber-600 hover:bg-amber-700"
                >
                  {selectedLocation.explored ? 'Revisiter' : 'Explorer'}
                </Button>
                <Button
                  variant="outline"
                  onClick={() => setSelectedLocation(null)}
                  className="border-slate-600 text-slate-300 hover:bg-slate-700"
                >
                  Fermer
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
}
