"use client";

import React, { useState } from "react";
import { GameProvider } from "@/lib/gameContext";
import ErrorBoundary from "@/components/ErrorBoundary";
import Sidebar from "@/components/Game/Sidebar";
import CharacterCreation from "@/components/Game/CharacterCreation";
import WorldMap from "@/components/Game/WorldMap";
import CombatArena from "@/components/Game/CombatArena";
import QuestJournal from "@/components/Game/QuestJournal";
import NPCDialogue from "@/components/Game/NPCDialogue";

export type GameSection = "creation" | "explore" | "combat" | "quests" | "npc";

export default function GamePage() {
  const [activeSection, setActiveSection] = useState<GameSection>("creation");

  const renderSection = () => {
    switch (activeSection) {
      case "creation":
        return <CharacterCreation onComplete={() => setActiveSection("explore")} />;
      case "explore":
        return <WorldMap />;
      case "combat":
        return <CombatArena />;
      case "quests":
        return <QuestJournal />;
      case "npc":
        return <NPCDialogue />;
      default:
        return (
          <div className="flex items-center justify-center h-full">
            <p className="text-slate-400">Section non trouvée dans les archives de l'Éclipse...</p>
          </div>
        );
    }
  };

  return (
    <GameProvider>
      <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 text-white">
        <header className="border-b border-slate-700 bg-slate-900/50 backdrop-blur-sm">
          <div className="container mx-auto px-4 py-4">
            <h1 className="text-2xl font-bold bg-gradient-to-r from-amber-400 to-orange-500 bg-clip-text text-transparent">
              Chroniques de l'Éclipse
            </h1>
            <p className="text-sm text-slate-400 mt-1">
              Les ombres de l'Éclipse Noire dansent encore...
            </p>
          </div>
        </header>
        
        <div className="flex h-[calc(100vh-80px)]">
          <Sidebar 
            activeSection={activeSection} 
            setActiveSection={setActiveSection} 
          />
          
          <ErrorBoundary>
            <main className="flex-1 overflow-auto">
              <div className="p-6 h-full">
                {renderSection()}
              </div>
            </main>
          </ErrorBoundary>
        </div>
      </div>
    </GameProvider>
  );
}
