"use client";

import React from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { useGame } from "@/lib/gameContext";
import type { GameSection } from "@/app/game/page";

interface SidebarProps {
  activeSection: GameSection;
  setActiveSection: (section: GameSection) => void;
}

const menuItems = [
  {
    id: "creation" as GameSection,
    title: "Création",
    description: "Forge ton destin",
    disabled: false,
  },
  {
    id: "explore" as GameSection,
    title: "Exploration",
    description: "Terres d'Auralis",
    disabled: false,
  },
  {
    id: "combat" as GameSection,
    title: "Arène",
    description: "Combat PvP",
    disabled: false,
  },
  {
    id: "quests" as GameSection,
    title: "Quêtes",
    description: "Journal épique",
    disabled: false,
  },
  {
    id: "npc" as GameSection,
    title: "Dialogue",
    description: "Rencontres mystiques",
    disabled: false,
  },
];

export default function Sidebar({ activeSection, setActiveSection }: SidebarProps) {
  const { state } = useGame();

  return (
    <aside className="w-64 bg-slate-900/80 backdrop-blur-sm border-r border-slate-700 p-4">
      <div className="space-y-3">
        {/* Character Info */}
        {state.character && (
          <Card className="p-3 bg-slate-800/50 border-slate-600">
            <div className="text-sm">
              <p className="font-semibold text-amber-400">{state.character.name}</p>
              <p className="text-slate-300 text-xs">{state.character.class}</p>
              <p className="text-slate-400 text-xs">{state.character.faction}</p>
              <div className="mt-2 space-y-1">
                <div className="flex justify-between text-xs">
                  <span>Niveau {state.character.level}</span>
                  <span className="text-green-400">{state.character.health}/{state.character.maxHealth} PV</span>
                </div>
                <div className="w-full bg-slate-700 rounded-full h-1">
                  <div 
                    className="bg-green-500 h-1 rounded-full transition-all duration-300"
                    style={{ width: `${(state.character.health / state.character.maxHealth) * 100}%` }}
                  />
                </div>
              </div>
            </div>
          </Card>
        )}

        {/* Location */}
        <Card className="p-3 bg-slate-800/30 border-slate-600">
          <p className="text-xs text-slate-400">Localisation actuelle</p>
          <p className="text-sm font-medium text-slate-200">{state.currentLocation}</p>
        </Card>

        {/* Navigation Menu */}
        <nav className="space-y-2">
          {menuItems.map((item) => {
            const isActive = activeSection === item.id;
            const isDisabled = item.disabled || (item.id !== "creation" && !state.character);
            
            return (
              <Button
                key={item.id}
                variant={isActive ? "default" : "ghost"}
                className={`w-full justify-start h-auto p-3 ${
                  isActive 
                    ? "bg-amber-600 hover:bg-amber-700 text-white" 
                    : "hover:bg-slate-800 text-slate-300"
                } ${isDisabled ? "opacity-50 cursor-not-allowed" : ""}`}
                onClick={() => !isDisabled && setActiveSection(item.id)}
                disabled={isDisabled}
              >
                <div className="text-left">
                  <div className="font-medium">{item.title}</div>
                  <div className="text-xs opacity-80">{item.description}</div>
                </div>
              </Button>
            );
          })}
        </nav>

        {/* Game Stats */}
        {state.character && (
          <Card className="p-3 bg-slate-800/30 border-slate-600 mt-4">
            <p className="text-xs text-slate-400 mb-2">Statistiques</p>
            <div className="space-y-1 text-xs">
              <div className="flex justify-between">
                <span>Quêtes actives</span>
                <span className="text-amber-400">{state.quests.filter(q => q.status === 'active').length}</span>
              </div>
              <div className="flex justify-between">
                <span>Objets</span>
                <span className="text-blue-400">{state.inventory.length}</span>
              </div>
              {state.isInCombat && (
                <div className="flex justify-between">
                  <span className="text-red-400">En combat</span>
                  <span className="text-red-400">⚔️</span>
                </div>
              )}
            </div>
          </Card>
        )}
      </div>
    </aside>
  );
}
