"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { useGame } from "@/lib/gameContext";

export default function NPCDialogue() {
  const { state, dispatch } = useGame();
  const [message, setMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [apiKey, setApiKey] = useState("");
  const [showApiKeyInput, setShowApiKeyInput] = useState(false);

  const sendMessage = async () => {
    if (!message.trim() || isLoading) return;
    
    if (!apiKey) {
      setShowApiKeyInput(true);
      return;
    }

    const userMessage = message.trim();
    setMessage("");
    setIsLoading(true);

    // Add user message to dialogue
    dispatch({
      type: 'ADD_NPC_MESSAGE',
      payload: { speaker: 'Vous', message: userMessage }
    });

    try {
      const response = await fetch('https://openrouter.ai/api/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${apiKey}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          model: 'openai/gpt-4o',
          messages: [
            {
              role: 'system',
              content: `Tu es un PNJ mystérieux dans le monde d'Auralis, marqué par l'Éclipse Noire. Tu es un ancien sage qui a survécu à la catastrophe et qui possède des connaissances sur les secrets de l'Éclipse. Tu parles de manière énigmatique et poétique, en français. Tu peux donner des conseils, raconter des légendes, ou révéler des indices sur les mystères du monde. Le joueur s'appelle ${state.character?.name || 'Aventurier'} et appartient à la faction ${state.character?.faction || 'inconnue'}.`
            },
            {
              role: 'user',
              content: userMessage
            }
          ],
          max_tokens: 200,
          temperature: 0.8
        })
      });

      if (!response.ok) {
        throw new Error(`Erreur API: ${response.status}`);
      }

      const data = await response.json();
      const npcResponse = data.choices[0]?.message?.content || "Les vents de l'Éclipse emportent mes mots...";

      // Add NPC response to dialogue
      dispatch({
        type: 'ADD_NPC_MESSAGE',
        payload: { speaker: 'Sage de l\'Éclipse', message: npcResponse }
      });

    } catch (error) {
      console.error('Erreur lors de l\'appel API:', error);
      dispatch({
        type: 'ADD_NPC_MESSAGE',
        payload: { 
          speaker: 'Sage de l\'Éclipse', 
          message: "Les énergies de l'Éclipse perturbent notre communication... Réessayez plus tard." 
        }
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const clearDialogue = () => {
    dispatch({ type: 'CLEAR_NPC_DIALOGUE' });
  };

  if (!state.character) {
    return (
      <div className="text-center py-12">
        <p className="text-slate-400">Vous devez créer un personnage avant de pouvoir dialoguer avec les PNJ.</p>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-amber-400 mb-2">Dialogue Mystique</h2>
        <p className="text-slate-300">
          Conversez avec les êtres anciens qui ont survécu à l'Éclipse Noire
        </p>
      </div>

      {/* API Key Input */}
      {showApiKeyInput && (
        <Card className="mb-6 bg-slate-800/50 border-slate-600">
          <CardHeader>
            <CardTitle className="text-amber-400">Configuration API</CardTitle>
            <CardDescription>
              Entrez votre clé API OpenRouter pour activer les dialogues IA
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <Input
              type="password"
              placeholder="Votre clé API OpenRouter..."
              value={apiKey}
              onChange={(e) => setApiKey(e.target.value)}
              className="bg-slate-700 border-slate-600 text-white"
            />
            <div className="flex gap-2">
              <Button
                onClick={() => setShowApiKeyInput(false)}
                disabled={!apiKey}
                className="bg-amber-600 hover:bg-amber-700"
              >
                Confirmer
              </Button>
              <Button
                variant="outline"
                onClick={() => setShowApiKeyInput(false)}
                className="border-slate-600 text-slate-300 hover:bg-slate-700"
              >
                Annuler
              </Button>
            </div>
            <p className="text-xs text-slate-400">
              Votre clé API est stockée localement et n'est pas partagée.
            </p>
          </CardContent>
        </Card>
      )}

      <div className="grid lg:grid-cols-3 gap-6">
        {/* Chat Interface */}
        <div className="lg:col-span-2">
          <Card className="bg-slate-800/50 border-slate-600">
            <CardHeader>
              <CardTitle className="text-purple-400">Sage de l'Éclipse</CardTitle>
              <CardDescription>
                Un être ancien aux connaissances mystérieuses
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              {/* Chat History */}
              <ScrollArea className="h-96 w-full border border-slate-600 rounded-lg p-4 bg-slate-900/50">
                {state.npcDialogue.length === 0 ? (
                  <div className="text-center py-8">
                    <p className="text-slate-400 mb-4">
                      Le Sage vous observe silencieusement, attendant que vous preniez la parole...
                    </p>
                    <p className="text-sm text-slate-500">
                      Commencez la conversation en tapant votre message ci-dessous.
                    </p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {state.npcDialogue.map((msg, index) => (
                      <div
                        key={index}
                        className={`flex ${msg.speaker === 'Vous' ? 'justify-end' : 'justify-start'}`}
                      >
                        <div
                          className={`max-w-[80%] p-3 rounded-lg ${
                            msg.speaker === 'Vous'
                              ? 'bg-amber-600 text-white'
                              : 'bg-slate-700 text-slate-200'
                          }`}
                        >
                          <p className="text-sm font-medium mb-1">{msg.speaker}</p>
                          <p className="text-sm">{msg.message}</p>
                        </div>
                      </div>
                    ))}
                    {isLoading && (
                      <div className="flex justify-start">
                        <div className="bg-slate-700 text-slate-200 p-3 rounded-lg">
                          <p className="text-sm font-medium mb-1">Sage de l'Éclipse</p>
                          <p className="text-sm">Les énergies mystiques s'alignent...</p>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </ScrollArea>

              {/* Message Input */}
              <div className="flex gap-2">
                <Input
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder="Tapez votre message..."
                  disabled={isLoading}
                  className="bg-slate-700 border-slate-600 text-white placeholder:text-slate-400"
                />
                <Button
                  onClick={sendMessage}
                  disabled={!message.trim() || isLoading}
                  className="bg-purple-600 hover:bg-purple-700"
                >
                  {isLoading ? 'Envoi...' : 'Envoyer'}
                </Button>
              </div>

              {/* Controls */}
              <div className="flex gap-2">
                <Button
                  onClick={clearDialogue}
                  variant="outline"
                  size="sm"
                  className="border-slate-600 text-slate-300 hover:bg-slate-700"
                >
                  Effacer l'historique
                </Button>
                <Button
                  onClick={() => setShowApiKeyInput(true)}
                  variant="outline"
                  size="sm"
                  className="border-slate-600 text-slate-300 hover:bg-slate-700"
                >
                  Changer API Key
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Character Info & Tips */}
        <div className="space-y-6">
          {/* Character Context */}
          <Card className="bg-slate-800/50 border-slate-600">
            <CardHeader>
              <CardTitle className="text-amber-400">Votre Identité</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 text-sm">
              <div>
                <span className="text-slate-400">Nom:</span>
                <p className="text-white font-medium">{state.character.name}</p>
              </div>
              <div>
                <span className="text-slate-400">Faction:</span>
                <p className="text-white">{state.character.faction}</p>
              </div>
              <div>
                <span className="text-slate-400">Classe:</span>
                <p className="text-white">{state.character.class}</p>
              </div>
              <div>
                <span className="text-slate-400">Niveau:</span>
                <p className="text-white">{state.character.level}</p>
              </div>
            </CardContent>
          </Card>

          {/* Conversation Tips */}
          <Card className="bg-slate-800/50 border-slate-600">
            <CardHeader>
              <CardTitle className="text-blue-400">Conseils de Dialogue</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3 text-sm">
              <div>
                <p className="text-slate-300 font-medium">Sujets à explorer:</p>
                <ul className="text-slate-400 space-y-1 mt-2">
                  <li>• L'histoire de l'Éclipse Noire</li>
                  <li>• Les secrets des factions</li>
                  <li>• Les artefacts mystiques</li>
                  <li>• Les prophéties anciennes</li>
                  <li>• Votre destinée personnelle</li>
                </ul>
              </div>
              <div>
                <p className="text-slate-300 font-medium">Le Sage peut:</p>
                <ul className="text-slate-400 space-y-1 mt-2">
                  <li>• Révéler des indices sur les quêtes</li>
                  <li>• Partager des légendes oubliées</li>
                  <li>• Donner des conseils stratégiques</li>
                  <li>• Interpréter vos rêves et visions</li>
                </ul>
              </div>
            </CardContent>
          </Card>

          {/* Dialogue Stats */}
          <Card className="bg-slate-800/50 border-slate-600">
            <CardHeader>
              <CardTitle className="text-green-400">Statistiques</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 text-sm">
              <div className="flex justify-between">
                <span className="text-slate-400">Messages échangés:</span>
                <span className="text-green-400">{state.npcDialogue.length}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-slate-400">Conversations:</span>
                <span className="text-green-400">
                  {state.npcDialogue.length > 0 ? '1 active' : 'Aucune'}
                </span>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
