"use client";

import React, { Component, ErrorInfo, ReactNode } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';

interface Props {
  children: ReactNode;
}

interface State {
  hasError: boolean;
  error?: Error;
}

class ErrorBoundary extends Component<Props, State> {
  public state: State = {
    hasError: false,
  };

  public static getDerivedStateFromError(error: Error): State {
    return { hasError: true, error };
  }

  public componentDidCatch(error: Error, errorInfo: ErrorInfo) {
    console.error('Game Error:', error, errorInfo);
  }

  private handleReset = () => {
    this.setState({ hasError: false, error: undefined });
  };

  public render() {
    if (this.state.hasError) {
      return (
        <div className="flex items-center justify-center min-h-[400px] p-4">
          <Card className="max-w-md w-full bg-red-950/20 border-red-800">
            <CardHeader>
              <CardTitle className="text-red-400">Erreur du Jeu</CardTitle>
              <CardDescription className="text-red-300">
                Une erreur inattendue s'est produite dans les Chroniques de l'Éclipse.
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <p className="text-sm text-slate-300">
                Les forces de l'Éclipse ont perturbé le flux du temps. Veuillez réessayer.
              </p>
              {this.state.error && (
                <details className="text-xs text-slate-400">
                  <summary className="cursor-pointer hover:text-slate-300">
                    Détails techniques
                  </summary>
                  <pre className="mt-2 p-2 bg-slate-900 rounded text-xs overflow-auto">
                    {this.state.error.message}
                  </pre>
                </details>
              )}
              <Button 
                onClick={this.handleReset}
                className="w-full bg-red-600 hover:bg-red-700"
              >
                Restaurer le Temps
              </Button>
            </CardContent>
          </Card>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;
