"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useGame } from "@/lib/gameContext";

export default function QuestJournal() {
  const { state, dispatch } = useGame();

  const activeQuests = state.quests.filter(q => q.status === 'active');
  const completedQuests = state.quests.filter(q => q.status === 'completed');
  const failedQuests = state.quests.filter(q => q.status === 'failed');

  const completeQuest = (questId: string) => {
    dispatch({
      type: 'UPDATE_QUEST',
      payload: {
        id: questId,
        updates: { status: 'completed' }
      }
    });

    // Add completion rewards
    if (state.character) {
      dispatch({
        type: 'UPDATE_CHARACTER',
        payload: { experience: state.character.experience + 100 }
      });
      dispatch({ type: 'ADD_TO_INVENTORY', payload: 'Récompense de quête' });
    }
  };

  if (!state.character) {
    return (
      <div className="text-center py-12">
        <p className="text-slate-400">Vous devez créer un personnage avant d'accéder au journal de quêtes.</p>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-amber-400 mb-2">Journal de Quêtes</h2>
        <p className="text-slate-300">
          Suivez votre progression et façonnez votre destinée à travers vos choix
        </p>
      </div>

      <div className="grid lg:grid-cols-3 gap-6">
        {/* Quest Tabs */}
        <div className="lg:col-span-2">
          <Tabs defaultValue="active" className="w-full">
            <TabsList className="grid w-full grid-cols-3 bg-slate-800">
              <TabsTrigger value="active" className="data-[state=active]:bg-amber-600">
                Actives ({activeQuests.length})
              </TabsTrigger>
              <TabsTrigger value="completed" className="data-[state=active]:bg-green-600">
                Terminées ({completedQuests.length})
              </TabsTrigger>
              <TabsTrigger value="failed" className="data-[state=active]:bg-red-600">
                Échouées ({failedQuests.length})
              </TabsTrigger>
            </TabsList>

            <TabsContent value="active" className="space-y-4">
              {activeQuests.length === 0 ? (
                <Card className="bg-slate-800/50 border-slate-600">
                  <CardContent className="pt-6 text-center">
                    <p className="text-slate-400">Aucune quête active</p>
                  </CardContent>
                </Card>
              ) : (
                activeQuests.map((quest) => (
                  <Card key={quest.id} className="bg-slate-800/50 border-slate-600">
                    <CardHeader>
                      <CardTitle className="text-amber-400">{quest.title}</CardTitle>
                      <CardDescription className="text-slate-300">
                        {quest.description}
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-3">
                        <div>
                          <div className="flex justify-between text-sm mb-1">
                            <span>Progression</span>
                            <span>{quest.progress}/{quest.maxProgress}</span>
                          </div>
                          <Progress value={(quest.progress / quest.maxProgress) * 100} />
                        </div>
                        
                        {quest.progress >= quest.maxProgress && (
                          <Button
                            onClick={() => completeQuest(quest.id)}
                            className="w-full bg-green-600 hover:bg-green-700"
                          >
                            Terminer la Quête
                          </Button>
                        )}
                      </div>
                    </CardContent>
                  </Card>
                ))
              )}
            </TabsContent>

            <TabsContent value="completed" className="space-y-4">
              {completedQuests.map((quest) => (
                <Card key={quest.id} className="bg-slate-800/30 border-slate-600">
                  <CardHeader>
                    <CardTitle className="text-green-400">{quest.title}</CardTitle>
                    <CardDescription className="text-slate-300">
                      {quest.description}
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <p className="text-sm text-green-400">✓ Terminée</p>
                  </CardContent>
                </Card>
              ))}
            </TabsContent>

            <TabsContent value="failed" className="space-y-4">
              {failedQuests.map((quest) => (
                <Card key={quest.id} className="bg-slate-800/30 border-slate-600">
                  <CardHeader>
                    <CardTitle className="text-red-400">{quest.title}</CardTitle>
                    <CardDescription className="text-slate-300">
                      {quest.description}
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <p className="text-sm text-red-400">✗ Échouée</p>
                  </CardContent>
                </Card>
              ))}
            </TabsContent>
          </Tabs>
        </div>

        {/* Character Info */}
        <div className="space-y-6">
          {/* Character Progress */}
          <Card className="bg-slate-800/50 border-slate-600">
            <CardHeader>
              <CardTitle className="text-amber-400">Progression</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div>
                <div className="flex justify-between text-sm mb-1">
                  <span>Expérience</span>
                  <span>{state.character.experience} EXP</span>
                </div>
                <Progress value={(state.character.experience % 100)} />
                <p className="text-xs text-slate-400 mt-1">
                  {100 - (state.character.experience % 100)} EXP pour le niveau suivant
                </p>
              </div>
              
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-slate-400">Quêtes actives:</span>
                  <p className="text-amber-400 font-semibold">{activeQuests.length}</p>
                </div>
                <div>
                  <span className="text-slate-400">Terminées:</span>
                  <p className="text-green-400 font-semibold">{completedQuests.length}</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Inventory Preview */}
          <Card className="bg-slate-800/50 border-slate-600">
            <CardHeader>
              <CardTitle className="text-blue-400">Inventaire</CardTitle>
            </CardHeader>
            <CardContent>
              <ScrollArea className="h-32">
                {state.inventory.length === 0 ? (
                  <p className="text-slate-400 text-sm">Inventaire vide</p>
                ) : (
                  <div className="space-y-1">
                    {state.inventory.map((item, index) => (
                      <div key={index} className="text-sm bg-slate-700 px-2 py-1 rounded">
                        {item}
                      </div>
                    ))}
                  </div>
                )}
              </ScrollArea>
            </CardContent>
          </Card>

          {/* Quest Tips */}
          <Card className="bg-slate-800/50 border-slate-600">
            <CardHeader>
              <CardTitle className="text-purple-400">Conseils</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 text-sm">
              <p className="text-slate-300">
                • Explorez le monde pour découvrir de nouvelles quêtes
              </p>
              <p className="text-slate-300">
                • Combattez des ennemis pour gagner de l'expérience
              </p>
              <p className="text-slate-300">
                • Dialoguez avec les PNJ pour obtenir des indices
              </p>
              <p className="text-slate-300">
                • Vos choix influencent votre destinée
              </p>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
