"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { useGame } from "@/lib/gameContext";

interface CharacterCreationProps {
  onComplete: () => void;
}

const factions = [
  {
    id: "ordre-lumiere",
    name: "Ordre de la Lumière",
    description: "Gardiens de l'ancienne magie, ils cherchent à restaurer l'équilibre d'Auralis.",
    bonus: "+2 Magie, +1 Défense"
  },
  {
    id: "legion-ombre",
    name: "Légion de l'Ombre",
    description: "Maîtres des arts sombres, ils embrassent le pouvoir de l'Éclipse Noire.",
    bonus: "+2 Attaque, +1 Vitesse"
  },
  {
    id: "gardiens-nature",
    name: "Gardiens de la Nature",
    description: "Protecteurs des terres sauvages, ils puisent leur force dans les éléments.",
    bonus: "+2 Endurance, +1 Régénération"
  },
  {
    id: "mercenaires-libres",
    name: "Mercenaires Libres",
    description: "Sans allégeance, ils naviguent entre les factions selon leurs intérêts.",
    bonus: "+1 à toutes les statistiques"
  }
];

const classes = [
  {
    id: "guerrier",
    name: "Guerrier de l'Éclipse",
    description: "Maître des armes, résistant aux forces obscures.",
    stats: "PV: 120, Attaque: 15, Défense: 12"
  },
  {
    id: "mage",
    name: "Tisseur d'Arcanes",
    description: "Manipule les énergies mystiques avec une précision mortelle.",
    stats: "PV: 80, Magie: 18, Mana: 100"
  },
  {
    id: "rodeur",
    name: "Rôdeur des Ruines",
    description: "Expert en survie et en combat à distance.",
    stats: "PV: 100, Vitesse: 16, Précision: 14"
  },
  {
    id: "assassin",
    name: "Lame de l'Ombre",
    description: "Frappe depuis les ténèbres avec une efficacité redoutable.",
    stats: "PV: 90, Vitesse: 18, Critique: 20%"
  }
];

export default function CharacterCreation({ onComplete }: CharacterCreationProps) {
  const { state, dispatch } = useGame();
  const [formData, setFormData] = useState({
    name: "",
    faction: "",
    class: ""
  });
  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    
    if (!formData.name.trim()) {
      newErrors.name = "Le nom de votre personnage est requis";
    } else if (formData.name.length < 2) {
      newErrors.name = "Le nom doit contenir au moins 2 caractères";
    } else if (formData.name.length > 20) {
      newErrors.name = "Le nom ne peut pas dépasser 20 caractères";
    }
    
    if (!formData.faction) {
      newErrors.faction = "Vous devez choisir une faction";
    }
    
    if (!formData.class) {
      newErrors.class = "Vous devez choisir une classe";
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    try {
      dispatch({
        type: 'CREATE_CHARACTER',
        payload: {
          name: formData.name.trim(),
          faction: formData.faction,
          class: formData.class,
          attack: 10,
          defense: 8,
        }
      });

      // Add initial quest
      dispatch({
        type: 'ADD_QUEST',
        payload: {
          id: 'welcome-quest',
          title: 'Les Premiers Pas',
          description: 'Explorez les terres désolées et découvrez les secrets de l\'Éclipse Noire.',
          status: 'active',
          progress: 0,
          maxProgress: 3
        }
      });

      onComplete();
    } catch (error) {
      console.error('Erreur lors de la création du personnage:', error);
      setErrors({ general: 'Une erreur est survenue lors de la création du personnage' });
    }
  };

  if (state.character) {
    return (
      <div className="max-w-2xl mx-auto">
        <Card className="bg-slate-800/50 border-slate-600">
          <CardHeader>
            <CardTitle className="text-amber-400">Personnage Créé</CardTitle>
            <CardDescription>Votre héros est prêt à affronter les mystères d'Auralis</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <Label className="text-slate-300">Nom</Label>
                <p className="text-white font-semibold">{state.character.name}</p>
              </div>
              <div>
                <Label className="text-slate-300">Niveau</Label>
                <p className="text-white font-semibold">{state.character.level}</p>
              </div>
              <div>
                <Label className="text-slate-300">Faction</Label>
                <p className="text-white">{state.character.faction}</p>
              </div>
              <div>
                <Label className="text-slate-300">Classe</Label>
                <p className="text-white">{state.character.class}</p>
              </div>
            </div>
            <div className="pt-4">
              <p className="text-slate-300 text-sm">
                Votre aventure commence maintenant. Explorez le monde d'Auralis et découvrez votre destinée.
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-amber-400 mb-2">Forge ton Destin</h2>
        <p className="text-slate-300">
          Dans les ruines d'Auralis, chaque choix façonne votre légende. Qui deviendrez-vous ?
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-8">
        {errors.general && (
          <Card className="bg-red-950/20 border-red-800">
            <CardContent className="pt-6">
              <p className="text-red-400">{errors.general}</p>
            </CardContent>
          </Card>
        )}

        {/* Character Name */}
        <Card className="bg-slate-800/50 border-slate-600">
          <CardHeader>
            <CardTitle className="text-white">Identité</CardTitle>
            <CardDescription>Comment les légendes se souviendront-elles de vous ?</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <Label htmlFor="name" className="text-slate-300">Nom du Personnage</Label>
              <Input
                id="name"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                placeholder="Entrez le nom de votre héros..."
                className="bg-slate-700 border-slate-600 text-white placeholder:text-slate-400"
              />
              {errors.name && <p className="text-red-400 text-sm">{errors.name}</p>}
            </div>
          </CardContent>
        </Card>

        {/* Faction Selection */}
        <Card className="bg-slate-800/50 border-slate-600">
          <CardHeader>
            <CardTitle className="text-white">Allégeance</CardTitle>
            <CardDescription>Choisissez votre camp dans la guerre des factions</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <Select value={formData.faction} onValueChange={(value) => setFormData({ ...formData, faction: value })}>
                <SelectTrigger className="bg-slate-700 border-slate-600 text-white">
                  <SelectValue placeholder="Sélectionnez une faction..." />
                </SelectTrigger>
                <SelectContent className="bg-slate-800 border-slate-600">
                  {factions.map((faction) => (
                    <SelectItem key={faction.id} value={faction.id} className="text-white hover:bg-slate-700">
                      {faction.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              
              {formData.faction && (
                <div className="mt-4">
                  {factions.map((faction) => 
                    faction.id === formData.faction && (
                      <div key={faction.id} className="p-4 bg-slate-700/50 rounded-lg">
                        <h4 className="font-semibold text-amber-400 mb-2">{faction.name}</h4>
                        <p className="text-slate-300 text-sm mb-2">{faction.description}</p>
                        <p className="text-green-400 text-sm font-medium">Bonus: {faction.bonus}</p>
                      </div>
                    )
                  )}
                </div>
              )}
              
              {errors.faction && <p className="text-red-400 text-sm">{errors.faction}</p>}
            </div>
          </CardContent>
        </Card>

        {/* Class Selection */}
        <Card className="bg-slate-800/50 border-slate-600">
          <CardHeader>
            <CardTitle className="text-white">Voie du Combattant</CardTitle>
            <CardDescription>Maîtrisez les arts qui définiront votre style de combat</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <Select value={formData.class} onValueChange={(value) => setFormData({ ...formData, class: value })}>
                <SelectTrigger className="bg-slate-700 border-slate-600 text-white">
                  <SelectValue placeholder="Choisissez votre classe..." />
                </SelectTrigger>
                <SelectContent className="bg-slate-800 border-slate-600">
                  {classes.map((cls) => (
                    <SelectItem key={cls.id} value={cls.id} className="text-white hover:bg-slate-700">
                      {cls.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              
              {formData.class && (
                <div className="mt-4">
                  {classes.map((cls) => 
                    cls.id === formData.class && (
                      <div key={cls.id} className="p-4 bg-slate-700/50 rounded-lg">
                        <h4 className="font-semibold text-amber-400 mb-2">{cls.name}</h4>
                        <p className="text-slate-300 text-sm mb-2">{cls.description}</p>
                        <p className="text-blue-400 text-sm font-medium">Statistiques: {cls.stats}</p>
                      </div>
                    )
                  )}
                </div>
              )}
              
              {errors.class && <p className="text-red-400 text-sm">{errors.class}</p>}
            </div>
          </CardContent>
        </Card>

        <div className="text-center">
          <Button 
            type="submit" 
            size="lg"
            className="bg-gradient-to-r from-amber-500 to-orange-600 hover:from-amber-600 hover:to-orange-700 text-white font-bold px-8 py-3"
          >
            Commencer l'Aventure
          </Button>
        </div>
      </form>
    </div>
  );
}
